import { HttpClient } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit } from '@angular/core';

import { Post } from '@playground/domain';
import { InfiniteScrollComponent } from '@playground/shared';

@Component({
  selector: 'playground-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'full-size d-flex flex-column'
  }
})
export class PostsComponent extends InfiniteScrollComponent<Post> implements OnInit {

  readonly itemSize = 101;

  override loadMoreMargin = this.itemSize * 4;
  override resourceUrl = 'https://jsonplaceholder.typicode.com/posts';

  constructor(
    http: HttpClient,
    cdr: ChangeDetectorRef,
    private readonly elementRef: ElementRef<HTMLElement>,
  ) {
    super(http, cdr);
  }

  override ngOnInit(): void {
    const maxItemsPerViewport = Math.floor(this.elementRef.nativeElement.offsetHeight / this.itemSize);
    this.pageSize = maxItemsPerViewport * 2;

    super.ngOnInit();
  }

}
